import React from "react";

type Props = {
  name: string;
  href?: string;
};

const Heading: React.FC<Props> = ({ name, href }) =>
  href ? (
    <a
      href={href}
      style={{ color: "gray" }}
      aria-label="Read more about Seminole tax hike"
    >
      <i className={`fa fa-${name} fa-2x`} />
    </a>
  ) : (
    <i className={`fa fa-${name} fa-2x`} />
  );

export default Heading;
